const service = document.querySelector('.service-scroll');
const contacts = document.querySelector('.contacts-scroll');
const price = document.querySelector('.price-scroll');
const about = document.querySelector('.about-us');

const body = document.querySelector('.page__body')
about.addEventListener('click', ()=>{
    if(body.scrollHeight<'4000'){
    window.scroll({ top: 688, left: 0, behavior: 'smooth' });}
    else if(body.scrollHeight>'6800' && body.scrollHeight<'7000' ){
        window.scroll({ top: 776, left: 0, behavior: 'smooth' });}
    else if(body.scrollHeight>'7000'){
        window.scroll({ top: 888, left: 0, behavior: 'smooth' });}

    else{
        window.scroll({ top: 779, left: 0, behavior: 'smooth' });
    }
});


service.addEventListener('click', ()=>{
    if(body.scrollHeight<'4000'){                                   //pc
    window.scroll({ top: 1376, left: 0, behavior: 'smooth' });}
    else if(body.scrollHeight>'6800' && body.scrollHeight<'7000' ){ //phone
        window.scroll({ top: 1640, left: 0, behavior: 'smooth' });}
    else if(body.scrollHeight>'7000'){                              //mini-phone
        window.scroll({ top: 1886, left: 0, behavior: 'smooth' });}

    else{
        window.scroll({ top: 1652, left: 0, behavior: 'smooth' }); //tablet
    }
});

